body { font-family: 'roboto'; font-size: 16px; background-color: #fff; overflow-x: hidden; min-width: 320px; }

body * { box-sizing: border-box; }

.container { max-width: 768px; margin: 0 auto; }

.header { border-bottom: 1px solid #d9d9d9; background-color: #fff; }

.menu { display: flex; height: 80px; justify-content: space-around; align-items: center; margin: 0 auto; width: 574px; }

.menu_item { text-decoration: none; font-size: 14px; font-weight: 300; color: #435993; }

.menu_item:hover { color: #1c1c1c; }

.first { height: 255px; padding-top: 45px; background: linear-gradient(to bottom, transparent 0%, transparent 25%, #fff 55%, #fff 100%), url("../img/first_bg.jpg"); background-size: cover; background-position: top center; display: flex; align-items: center; }

.first h1 { color: black; font-size: 70px; font-weight: 100; text-align: center; margin: 0; padding-left: 9px; }

.section_heading { color: #1c1c1c; text-align: center; font-size: 30px; font-weight: 300; padding-top: 66px; margin: 0 0 46px 0; }

.info .section_heading { background: url("../img/info_icon.svg") top center no-repeat; }

.info .personal { padding-left: 10px; }

.row:not(:last-child) { margin-bottom: 25px; }

.input { display: block; width: 100%; max-width: 520px; margin: 0 auto; height: 60px; border: 1px solid #e5e5e5; border-radius: 5px; font-size: 24px; font-weight: 400; padding: 0 10px; text-align: center; }

.input::-webkit-input-placeholder { color: #c8c8c8; }

.input:-moz-placeholder { color: #c8c8c8; }

.input::-moz-placeholder { color: #c8c8c8; }

.input:-ms-input-placeholder { color: #c8c8c8; }

.jq-selectbox__search input { display: block; margin: 20px auto 0; height: 30px; border: 1px solid #e5e5e5; border-radius: 5px; font-size: 18px; text-align: center; }

.jq-selectbox__search input::-webkit-input-placeholder { color: #c8c8c8; }

.jq-selectbox__search input:-moz-placeholder { color: #c8c8c8; }

.jq-selectbox__search input::-moz-placeholder { color: #c8c8c8; }

.jq-selectbox__search input:-ms-input-placeholder { color: #c8c8c8; }

.jq-selectbox { display: block; margin: 0 auto; height: 60px; border: 1px solid #e5e5e5; border-radius: 5px; width: 100%; max-width: 320px; cursor: pointer; }

.jq-selectbox.opened { border-radius: 5px 5px 0 0; }

.jq-selectbox__select, .jq-selectbox__select-text.placeholder { width: 100%; max-width: 320px; height: 58px; line-height: 54px; color: #c8c8c8; font-size: 24px; font-weight: 400; text-align: center; }

.jq-selectbox__select-text.placeholder { padding: 0 40px; }

.jq-selectbox__trigger { position: absolute; top: 0; right: 0; width: 40px; height: 100%; display: flex; justify-content: center; align-items: center; }

.jq-selectbox__trigger-arrow { background-image: url(../img/sprite.png); background-position: -23px 0px; width: 14px; height: 9px; margin: 0; transition: .25s; }

.opened .jq-selectbox__trigger-arrow { transform: rotate(180deg); }

.jq-selectbox__dropdown { width: calc(100% + 2px); background: #fff; border: 1px solid #e5e5e5; text-align: center; left: -1px; }

.jq-selectbox__dropdown ul { padding: 0; }

.jq-selectbox__dropdown ul li { color: #c8c8c8; font-size: 24px; font-weight: 400; text-align: center; }

section { margin-bottom: 76px; }

.skills { margin-bottom: 120px; }

.skills .section_heading { background: url("../img/skills_icon.svg") top center no-repeat; }

.checks { display: flex; justify-content: space-between; }

.checks .group { padding-right: 20px; min-width: 207px; }

.checks .group label { display: flex; align-items: center; margin-bottom: 15px; cursor: pointer; }

.checks .group label .l_mark { display: inline-block; width: 20px; height: 20px; background-color: white; border: 1px solid #435993; border-radius: 3px; margin-right: 10px; position: relative; }

.checks .group label span { color: black; font-size: 16px; font-weight: 300; line-height: 20px; }

.checks .group input[type="checkbox"]:checked + label .l_mark { background: url("../img/check.png") center center no-repeat; }

.hidden { position: absolute; visibility: hidden; opacity: 0; }

.js { margin-bottom: 103px; }

.js .section_heading { background: url("../img/js_icon.svg") top center no-repeat; }

.level { position: relative; top: -2px; }

.level_desc { position: relative; display: flex; align-items: flex-end; }

.level_desc_item { margin: 0; flex-basis: 25%; padding-right: 10px; position: relative; color: #2e211c; font-size: 13px; font-weight: 300; line-height: 19px; text-decoration: none; }

.level_desc_item:hover { color: #435993; }

.level_desc_item:not(:last-of-type):before { content: ""; display: block; width: 1px; height: 20px; border-left: 1px dotted black; position: absolute; left: 0; top: 100%; }

.level_desc_item:not(:last-of-type):after { content: ""; display: block; position: absolute; top: 100%; width: 100%; height: 60px; z-index: 6; }

.level_desc_item:last-of-type { padding-right: 0; text-align: right; }

.level_desc_item:last-of-type:before { content: ""; display: block; position: absolute; top: 100%; width: 100%; height: 60px; z-index: 6; }

.level_desc_item:last-of-type:after { content: ""; display: block; width: 1px; height: 20px; border-left: 1px dotted black; position: absolute; right: 0; top: 100%; }

.level .range { width: 100%; height: 25px; background-color: #e8e8e8; transform: rotate(-1deg) translateY(15px); position: relative; z-index: 5; }

.level .range:before { content: ""; display: block; width: 50%; height: 100%; transition: .6s; background: linear-gradient(to right, #ccb1f1, #8986c3); }

.level .range .line { position: absolute; width: 1px; height: 100%; bottom: 0; background-color: #fff; }

.level .range .line:nth-child(1) { left: 25%; }

.level .range .line:nth-child(2) { left: 50%; }

.level .range_wrap { width: 100%; height: 25px; overflow: hidden; position: relative; }

.level .curs { display: inline-block; position: absolute; top: 101%; transform: translate(-50%); transition: .6s; left: 50%; will-change: left; background-image: url(../img/sprite.png); background-position: 0px 0px; width: 23px; height: 26px; }

.l1 .curs { left: 0; }

.l2 .curs { left: 25%; }

.l3 .curs { left: 50%; }

.l4 .curs { left: 100%; }

.l1 .range:before { width: 0; }

.l2 .range:before { width: 25%; }

.l3 .range:before { width: 50%; }

.l4 .range:before { width: 100%; }

.about { padding-left: 10px; margin-bottom: 63px; }

.about .section_heading { background: url("../img/about_icon.svg") top center no-repeat; margin-bottom: 8px; }

.about_inner { width: 520px; max-width: 100%; margin: 0 auto; }

.about_desc { color: #1c1c1c; font-size: 16px; font-weight: 300; line-height: 24px; text-align: center; margin: 0 0 40px 0; }

.textarea { width: 100%; height: 180px; border: 1px solid #e5e5e5; border-radius: 5px; font-size: 20px; font-weight: 400; padding: 15px; resize: none; }

.textarea::-webkit-input-placeholder { color: #c8c8c8; }

.textarea:-moz-placeholder { color: #c8c8c8; }

.textarea::-moz-placeholder { color: #c8c8c8; }

.textarea:-ms-input-placeholder { color: #c8c8c8; }

.submit { display: block; margin: 0 auto 50px; width: 115px; height: 50px; background-color: #435993; border-radius: 10px; color: white; font-size: 16px; font-weight: 300; border: none; transition: .25s; -webkit-appearance: none !important; }

.submit:hover { transform: scale(1.1); }

.menu_trigger { display: none; }

.mobile { display: none; }

@media screen and (max-width: 768px) { .container { padding: 0 10px; } }

@media screen and (max-width: 767px) { .desctop { display: none; }
  .mobile { display: inline-block; }
  .header { position: fixed; top: 0; left: -120%; width: 190px; background: #FFF; height: 100vh; padding-top: 30px; z-index: 100; transition: .4s; }
  .layer { visibility: hidden; opacity: 0; position: fixed; left: 0; right: 0; top: 0; bottom: 0; background-color: rgba(0, 0, 0, 0.5); transition: .4s; z-index: 50; }
  .menu_trigger { display: inline-block; width: 30px; height: 30px; border: 1px solid #fff; position: fixed; top: 20px; left: 20px; transition: .2s; z-index: 60; }
  .menu_trigger span { position: absolute; top: 50%; margin-top: -1px; left: 3px; width: 22px; height: 2px; background-color: #fff; transition: .2s; }
  .menu_trigger span:before, .menu_trigger span:after { content: ""; display: block; width: 100%; height: 100%; background-color: #fff; position: absolute; left: 0; transition: .2s; }
  .menu_trigger span:before { top: -7px; }
  .menu_trigger span:after { bottom: -7px; }
  .menu_trigger.black { border-color: #000; }
  .menu_trigger.black span { background-color: #000; }
  .menu_trigger.black span:before, .menu_trigger.black span:after { background-color: #000; }
  .menu_opened .header { left: 0; }
  .menu_opened .layer { visibility: visible; opacity: 1; }
  .menu { flex-direction: column; width: 100%; height: auto; }
  .menu_item { display: block; width: 100%; padding: 10px 15px; border-bottom: 1px solid #d9d9d9; }
  .info .personal { padding-left: 0; }
  .checks { flex-direction: column; align-items: center; }
  .about { padding-left: 0; }
  .range_list { padding: 10px 0 0 15px; font-size: 14px; }
  section { margin-bottom: 50px !important; }
  .first { padding-top: 0; } }
